* {
  box-sizing: border-box;
}

body {
  font-family: Outfit, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background-color: #fafafa; */
  color: #212121;
  line-height: 1.5;
  background-color: #FFDEE9;
  background-image: linear-gradient(90deg, #FFDEE9 0%, #B5FFFC 100%);

}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list {
  list-style: none;
  padding: 0;
}

.country-item {
  margin-bottom: 15px;
}

.mg-30 {
  margin-bottom: 30px;
}

.country-image {
  margin-right: 10px;
}

.country-name {
  display: inline-block;
  font-size: 20px;
  font-weight: normal;
}

.country-info > .country-name {
  font-size: 30px;
  font-weight: 800;
}

.country-text {
  font-size: 20px;
  margin-top: 10px;
  margin-bottom: 10px;
}